import './HomePage.css'
import { styled } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import { useMediaQuery } from '@mui/material';

import flower1 from '../../assets/flores-1.png'; // Importe imagens das flores
import flower2 from '../../assets/flores-2.png';
import flower3 from '../../assets/flores-3.png';
import Flower from '../../components/Flower/Flower';
import { Key, useEffect, useState } from 'react';

const Item = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
}));

const StyledPaper = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  backgroundColor: 'rgb(253, 249, 22);',
  padding: theme.spacing(2),
  maxWidth: 400,
  textAlign: 'center',
  color: theme.palette.text.primary,
}));


interface FlowerPosition {
  src: string;
  top: string;
  left: string;
}


const HomePage = () => {

  const isSmallScreen = useMediaQuery('(max-width: 900px)');
  const [flowers, setFlowers] = useState<FlowerPosition[]>([]);
  
  useEffect(() => {
    const calculateFlowerPositions = () => {
      const homePageElement = document.querySelector('.HomePage') as HTMLElement | null;
      if (!homePageElement) return [];

      const { offsetWidth, offsetHeight } = homePageElement;

      const calculatedFlowers = [
        // Flower 1
        { src: flower1, top: `${offsetHeight * 0.08}px`, left: `${offsetWidth * 0.001}px` },
        { src: flower1, top: `${offsetHeight * 0.2}px`, left: `${offsetWidth * 0.07}px` },
        { src: flower1, top: `${offsetHeight * 0.5}px`, left: `${offsetWidth * 0.9}px` },
        { src: flower1, top: `${offsetHeight * 0.7}px`, left: `${offsetWidth * 0.97}px` },
        { src: flower1, top: `${offsetHeight * 0.95}px`, left: `${offsetWidth * 0.05}px` },

        // Flower 2
        { src: flower2, top: `${offsetHeight * 0.15}px`, left: `${offsetWidth * 0.02}px` },
        { src: flower2, top: `${offsetHeight * 0.4}px`, left: `${offsetWidth * 0.9}px` },
        { src: flower2, top: `${offsetHeight * 0.6}px`, left: `${offsetWidth * 0.05}px` },
        { src: flower2, top: `${offsetHeight * 0.8}px`, left: `${offsetWidth * 0.93}px` },
        { src: flower2, top: `${offsetHeight * 0.99}px`, left: `${offsetWidth * 0.06}px` },

        // Flower 3
        { src: flower3, top: `${offsetHeight * 0.25}px`, left: `${offsetWidth * 0.05}px` },
        { src: flower3, top: `${offsetHeight * 0.55}px`, left: `${offsetWidth * 0.92}px` },
        { src: flower3, top: `${offsetHeight * 0.75}px`, left: `${offsetWidth * 0.02}px` },
        { src: flower3, top: `${offsetHeight * 0.85}px`, left: `${offsetWidth * 0.95}px` },
        { src: flower3, top: `${offsetHeight * 0.98}px`, left: `${offsetWidth * 0.03}px` },
      ];

      setFlowers(calculatedFlowers);
    };

    calculateFlowerPositions();
  }, []);





  return (
    <>
      <div className='HomePage'>
        {flowers.map((flower: { src: string; top: string; left: string; }, index: Key | null | undefined) => (
          <Flower key={index} src={flower.src} top={flower.top} left={flower.left} />
        ))}
        <div className='lateral-left'></div>
        <div className='content'>
          <div style={{ textAlign: "center" }}>
            <h1>Incluso em Ao: Uma Jornada de Transformao atravs da Ao Comunitria e Solidariedade</h1>

          </div>
          <p>No corao do distrito de Braga, um grupo diversificado de jovens apaixonados, provenientes de diferentes reas de formao, uniu-se em prol de uma causa comum: a promoo da incluso e a transformao de realidades. Movidos por uma viso compartilhada de um mundo mais justo e igualitrio, esses jovens visionrios deram vida a uma iniciativa sem precedentes: um grupo promotor de entreajuda e mudana, que tece uma aliana nica entre Ao Comunitria e Solidariedade.</p>

          <div style={{ display: "flex", flexDirection: isSmallScreen ? "column" : "row", gap: "10px", textAlign: "center", marginBottom: "10px" }}>
            <div>
              <h2>Origens e Inspirao</h2>
              <p>No dia 4 de Julho de 2023, marcou-se o nascimento desta iniciativa revolucionria com a realizao da revista inaugural "Todos Somos Especiais - Perspetivas da Incluso em Contexto Escolar". Este evento emblemtico no apenas sinalizou o incio de uma jornada significativa, mas tambm estabeleceu os alicerces para uma misso extraordinria que transcende os limites convencionais da atuao social.</p>

            </div>
            <div>
              <h2>Misso: Rumo a um Mundo Mais Inclusivo</h2>
              <p>A nossa misso  clara e urgente: trabalhar incansavelmente para promover a incluso em todas as suas formas, desafiando esteretipos, quebrando barreiras e construindo pontes de compreenso e empatia. Atravs da sinergia entre Ao Comunitria e Solidariedade, aspiramos no apenas a sensibilizar, mas tambm a inspirar ao concreta em direo a uma sociedade mais justa e acolhedora para todos.</p>

            </div>

          </div>

          <h3>Objetivos e Valores</h3>
          <ul>
            <li><strong>Objetivo Geral:</strong> Fomentar uma cultura de incluso e diversidade em todos os mbitos da sociedade, com nfase especial na ao comunitria e na solidariedade como ferramentas de transformao social.</li>
            <li><strong>Objetivos Especficos:</strong>
              <ul>
                <li>Sensibilizar e educar a comunidade sobre a importncia da incluso e da valorizao da diversidade.</li>
                <li>Promover o acesso equitativo  educao e s oportunidades para todos os membros da sociedade, independentemente de suas origens ou condies.</li>
                <li>Capacitar os jovens para se tornarem agentes de mudana, incentivando-os a usar suas habilidades e paixes para promover o bem comum.</li>
                <li>Estimular o dilogo intercultural e a colaborao entre diferentes grupos e comunidades, visando  construo de sociedades mais coesas e solidrias.</li>
              </ul>
            </li>

          </ul>

          <div className='valores'>
            <div className='title' style={{ textAlign: "center" }}>
              <h1>Os Nossos Valores</h1>
            </div>

            <Box sx={{ flexGrow: 1 }}>
              <Grid container spacing={2} justifyContent="center">
                <Grid item>
                  <StyledPaper sx={{ my: 1, p: 2, height: 250 }}>
                    <Typography variant="h1" gutterBottom fontWeight={700}>
                      Incluso
                    </Typography>
                    <Typography>
                      Acreditamos na dignidade e no valor intrnseco de cada indivduo, independentemente de suas caractersticas ou circunstncias
                    </Typography>
                  </StyledPaper>
                </Grid>
                <Grid item>
                  <StyledPaper sx={{ my: 1, p: 2, height: 250 }}>
                    <Typography variant="h1" gutterBottom fontWeight={700}>
                      Empatia
                    </Typography>
                    <Typography>
                      Comprometemo-nos a ouvir e compreender as experincias e perspectivas dos outros, cultivando um ambiente de respeito mtuo e compaixo
                    </Typography>
                  </StyledPaper>
                </Grid>
                <Grid item>
                  <StyledPaper sx={{ my: 1, p: 2, height: 250 }}>
                    <Typography variant="h1" gutterBottom fontWeight={700}>
                      Criatividade                    </Typography>
                    <Typography>
                      Reconhecemos o poder transformador da arte e da expresso criativa como ferramentas para a mudana social e pessoal
                    </Typography>
                  </StyledPaper>
                </Grid>
                <Grid item>
                  <StyledPaper sx={{ my: 1, p: 2, height: 250 }}>
                    <Typography variant="h1" gutterBottom fontWeight={700}>
                      Colaborao
                    </Typography>
                    <Typography>
                      Valorizamos a colaborao e o trabalho em equipe como meios eficazes para alcanar nossos objetivos e ampliar nosso impacto na comunidade
                    </Typography>
                  </StyledPaper>
                </Grid>
                <Grid item>
                  <StyledPaper sx={{ my: 1, p: 2, height: 250 }}>
                    <Typography variant="h1" gutterBottom fontWeight={700}>
                      Compromisso
                    </Typography>
                    <Typography>
                      Mantemos-nos firmes em nosso compromisso de agir com integridade, dedicao e determinao em busca de um mundo mais inclusivo e justo para todos
                    </Typography>
                  </StyledPaper>
                </Grid>
              </Grid>
            </Box>
          </div>

          <p style={{ marginTop: "50px", marginBottom: "0px" }}>Em resumo, estamos comprometidos em desafiar o status quo, inspirar a esperana e fazer a diferena, um passo de cada vez, em direo a um futuro onde a incluso no seja apenas uma ideia, mas sim uma realidade tangvel para todos. Junte-se a ns nesta jornada emocionante de transformao e descubra como, juntos, podemos criar um mundo mais inclusivo, acolhedor e compassivo para todos os seres humanos. </p>
        </div>
        <div className='lateral-right'> </div>

      </div>
    </>
  )
}
export default HomePage